@-webkit-keyframes autoplay {
    0% {
      opacity: 1;
    }
    10% {
      opacity: 1;
    }
    25% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    85% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes autoplay {
    0% {
      opacity: 1;
    }
    10% {
      opacity: 1;
    }
    25% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    85% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @-webkit-keyframes trans {
    0% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
      -webkit-transform: scale(1.3);
              transform: scale(1.3);
    }
  }
  @keyframes trans {
    0% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
      -webkit-transform: scale(1.3);
              transform: scale(1.3);
    }
  }

